.slider-tariff {
  position: relative;

  @media (min-width: $md) {
    padding-top: 2rem;
  }

  @media (min-width: $lg) {
    padding-top: 0;
    padding-bottom: 6rem;
  }

  & .swiper-wrapper {
    // flex-wrap: wrap;

    @media (max-width: $xs) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  & .swiper-pagination {
    top: 0;
    display: none;

    @media (min-width: $md) {
      display: none;
    }
  }

  & .swiper-pagination-bullet {
    background-color: #fff;
  }

  &__btns {
    display: none;
    position: absolute;
    top: 10px;
    right: 0;
    width: 110px;
    height: 50px;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $xs) {
      display: flex;
    }
    @media (min-width: $md) {
      top: 55px;
    }

    @media (min-width: $xl) {
      top: 10px;
    }

    @media (min-width: $lg) {
      display: flex;
      top: auto;
      bottom: 0;
      right: 50%;
      transform: translateX(50%);
      width: 150px;
    }
  }

  & .swiper-button-prev,
  & .swiper-button-next {
    position: relative;
    top: 0;
    width: 40px;
    height: 40px;
    background: $bg-gradient;
    border-radius: 50%;
    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.3s;

    @media (min-width: $lg) {
      width: 50px;
      height: 50px;
    }

    &:focus,
    &:hover {
      transform: scale(1.1);
      transition: 0.3s;
      background: $bg-gradient-revers;
    }

    & .icon {
      width: 24px;
      height: 20px;

      @media (min-width: $lg) {
        width: 28px;
        height: 24px;
      }
    }
  }
  & .swiper-button-next {
    & .icon {
      transform: rotate(180deg);
    }
  }
  & .swiper-button-prev::after,
  & .swiper-button-next::after {
    content: '';
    font-size: 0;
  }
}
