.products {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  width: 100%;
  margin: 0 auto;

  @media (min-width: $sm) {
    // padding-top: 100px;
  }
  @media (min-width: $xl) {
    // padding-top: 100px;
    padding-bottom: 100px;
  }

  & .bg-pic {
    position: absolute;

    &-3 {
      position: absolute;
      top: -3%;
      left: -47%;
      width: 420px;
      height: 420px;

      @media (min-width: $sm) {
        left: -20%;
      }
      @media (min-width: $xl) {
        top: 30%;
        left: 15%;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: 0;
        right: 10%;
        width: 420px;
        height: 420px;
        background-image: url('../images/profit/circle.webp');
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    &-2 {
      position: absolute;
      top: 35%;
      right: 12%;
      width: 237px;
      height: 237px;

      @media (min-width: $sm) {
        right: -20%;
      }
      @media (min-width: $md) {
        right: -10%;
      }
      @media (min-width: $xl) {
        top: 35%;
        right: 12%;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        bottom: 0;
        right: 10%;
        width: 237px;
        height: 237px;
        background-image: url('../images/profit/ellipse2.webp');
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    &-1 {
      position: absolute;
      top: 70%;
      left: 84%;
      width: 120px;
      height: 120px;

      @media (min-width: $sm) {
        left: -20%;
        width: 408px;
        height: 408px;
      }
      @media (min-width: $md) {
        top: 60%;
        left: -5%;
      }
      @media (min-width: $xl) {
        top: 0;
        left: 10%;
        width: 480px;
        height: 480px;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 2;
        bottom: 0;
        right: 10%;
        width: 100%;
        height: 100%;
        background-image: url('../images/profit/ellipse3.webp');
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 395px;
    height: auto;
    margin-bottom: 2rem;
    @media (min-width: $xs) {
      margin-bottom: 0;
    }
    @media (min-width: $sm) {
      width: calc(50% - 30px);
      height: 100%;
    }
  }
}
