.gallery {
  position: relative;
  padding-top: 50px;
  color: $white-color;
  overflow: hidden;
  background-color: $bg-block-gr;
  background-image: $bg-image-gradient;

  @media (min-width: $sm) {
    padding-top: 54px;
  }
  @media (min-width: $xl) {
    padding-top: 115px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;

    background-image: url('../images/profit/ellipse.webp');
    background-repeat: no-repeat;
    background-size: contain;
    transform: rotate(-180deg);
  }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 100%;

    background-image: url('../images/profit/ellipse.webp');
    background-repeat: no-repeat;
    background-size: contain;



    @media (min-width: $sm) {
      top: 70%;
    }
    @media (min-width: 1010px) {
      top: 50%;
    }
  }


  & .container {
    position: relative;
    z-index: 2;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;

    @media (min-width: $sm) {
      padding: 0;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    margin-bottom: 34px;
    padding: 0 15px;

    @media (min-width: $sm) {
      margin-bottom: 44px;
    }
    @media (min-width: $md) {
      margin-bottom: 54px;
      padding-left: 165px;
    }

    & .title {
      text-align: left;
      margin-bottom: 34px;
    }

  }

  &__item {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 54px;

    & picture,
    & img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    padding-left: 15px;
    padding-right: 15px;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 18px;
    line-height: 140%;
    color: $white-color;
    text-transform: uppercase;

    @media (min-width: $sm) {
      font-size: 20px;
    }
    @media (min-width: $md) {
      max-width: 640px;
    }
    @media (min-width: $sm) {
      margin-bottom: 44px;
    }
    @media (min-width: $xl) {
      margin-bottom: 33px;
    }


    &--mob {
      display: block;
      @media (min-width: $md) {
        display: none;
      }
    }
    & span {
      color: $primary-color;
    }
  }

  &__scroll {
    display: flex;
    display: grid;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    column-gap: 10px;
    padding-left: 15px;

    @media (min-width: $md) {
      display: grid;
      padding-left: 0;
    }
  }

  &__title {
    width: 100%;
    margin-bottom: 30px;

    @media (max-width: $sm) {
      max-width: 640px;
    }
    @media (min-width: $md) {
      padding-left: 165px;
    }
  }

  &__pic {
    display: flex;
    overflow: hidden;
    border-radius: 10px;

    width: 262px;
    height: 190px;

    @media (min-width: $sm) {
      width: 524px;
      height: 368px;
    }
    @media (min-width: $md) {
      width: auto;
      height: 245px;
      height: 300px;
      min-width: 368px;
    }

    & picture {
      @media (min-width: $sm) {
        height: 245px;
        height: 400px;
      }

      & img {
        height: 100%;
        @media (min-width: $sm) {
          height: 245px;
          height: 400px;
        }
      }
    }
  }

  &__item--pool {
    & .gallery__scroll {
      @media (min-width: $md) {
        padding-left: 165px;
        grid-template-columns: minmax(400px, 2fr) 1fr 2fr;
      }
      @media (min-width: $xl) {
        grid-template-columns: 2fr 1fr 3fr;
      }
    }
    & .slider-gallery {
      @media (min-width: $md) {
        margin-left: 165px;
      }
    }
  }

  &__item--gym,
  &__item--lessons {
    @media (min-width: $md) {
      // background-color: $white-color;
    }

    & .gallery__scroll {
      @media (min-width: $md) {
        padding-right: 165px;
        grid-template-columns: 2fr minmax(300px, 1fr) minmax(400px, 2fr);
      }
      @media (min-width: $xl) {
        grid-template-columns: 3fr 1fr 2fr;
      }
    }
  }

  &__item--spa {
    & .gallery__scroll {
      @media (min-width: $md) {
        padding-left: 165px;
        grid-template-columns: 2fr 1fr 2fr;
        align-items: flex-start;
        gap: 13px;
      }
    }
    & .slider-gallery {
      @media (min-width: $md) {
        margin-left: 165px;
      }
    }
  }
}

.mCustomScrollbar {
  &.scroll-gallery {
    & > div > div {
      display: grid;
      gap: 10px;
      grid-auto-flow: column; // без переноса на новыю строку
    }
  }
}

.scroll-gallery {
  display: grid;
  gap: 10px;
  grid-auto-flow: column; // без переноса на новыю строку
  overflow-x: auto;
}
