.profit {
  position: relative;
  // background-color: $bg-color;
  background-color: $bg-block-gr;
  overflow: hidden;
  background-image: $bg-image-gradient;

  @media (min-width: $xl) {
    padding-top: 100px;
  }

  &::before {
    content: '';

    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;

    background-image: url('../images/profit/ellipse.webp');
    background-repeat: no-repeat;
    background-size: contain;
    transform: rotate(-180deg);

    @media (min-width: $sm) {
      bottom: -10%;
    }
  }

  &::after {
    content: '';
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;
    width: 706px;
    height: 706px;

    @media (min-width: $sm) {
      bottom: -10%;
    }
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  & .container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1440px;

    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 15px;
    padding-right: 15px;

    @media (min-width: $md) {
      gap: 60px;
      padding-top: 0;
      padding-bottom: 54px;
    }
  }

  & .products {
    width: 100%;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 50px;

    @media (min-width: $sm) {
      margin-bottom: 60px;
    }

    @media (min-width: $md) {
      margin-bottom: 50px;
    }
  }

  &__title-pic {
    width: 300px;
    height: 250px;

    @media (min-width: $md) {
      width: 430px;
      height: 200px;
      max-width: 30%;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    width: 100%;

    @media (min-width: $md) {
      gap: 40px;
      width: 60%;
    }
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    padding: 15px;
    margin-right: auto;
    border: 1px solid #6ddcff;
    border-radius: 10px;

    & span {
      display: inline-flex;
    }
  }

  &__item-inner {
    position: relative;
    width: 100%;
    padding-left: 60px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      height: 50px;
      width: 50px;
      transform: translateY(-50%);
      background-image: var(--pic);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: contain;
    }
  }

  &__item-title {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: $primary-color;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 20px,
      )
    );
  }

  &__item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: $white-color;

    @include ff(
      'font-size',
      (
        320px: 16px,
        768px: 20px,
      )
    );
  }
}
