.promo {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;

  color: $white-color;
  overflow: hidden;
  background-color: $dark-color;

  transform-style: inherit;

  @media (min-width: $sm) {
    &::before {
      position: absolute;
      content: '';
      bottom: -50%;
      left: 20%;
      width: 494px;
      height: 494px;

      background-image: radial-gradient(
        50% 50% at 50% 50%,
        #1a61cb 76.56%,
        rgba(9, 39, 98, 0) 100%
      );
      filter: blur(162px);
    }
  }

  @media (min-width: $xl) {
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 10%;
      left: 0;
      width: 300px;
      height: 373px;
      background-image: url('../images/promo/04.jpg');
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  & .container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    max-width: 1440px;
    margin: 0;
    padding: 0 15px;

    // padding: 0 15px;
    padding-top: 24px;

    @media (min-width: $xs) {
      padding-left: 45px;
      padding-right: 45px;
    }
    @media (min-width: $sm) {
      padding-top: 62px;
    }
    @media (min-width: $md) {
      flex-direction: row;
    }
    @media (min-width: $xl) {
      padding-top: 75px;
    }
  }

  &__info {
    width: 100%;
    z-index: 4;
    position: relative;
    margin-bottom: 40px;
    text-align: center;

    @media (min-width: $xs) {
      text-align: left;
    }

    @media (min-width: $xl) {
      padding-left: 230px;
    }
  }

  &__title {
    max-width: 100%;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
    color: $primary-color;

    @include ff(
      'font-size',
      (
        320px: 18px,
        768px: 36px,
        1440px: 43px,
      )
    );

    @media (min-width: $sm) {
      line-height: 140%;
    }
    @media (min-width: $xl) {
      line-height: 110%;
    }
  }

  &__price {
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 38px;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 22px,
        768px: 36px,
        1200px: 52px,
      )
    );

    & span {
      @include ff(
        'font-size',
        (
          320px: 16px,
          768px: 26px,
          1200px: 32px,
        )
      );
    }
  }

  &__text {
    max-width: 100%;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    text-transform: uppercase;

    @include ff(
      'font-size',
      (
        320px: 30px,
        768px: 67px,
        1440px: 70px,
      )
    );

    @media (min-width: $sm) {
      line-height: 120%;
    }
  }

  &__pic {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 350px;
    min-height: 348px;
    margin-left: auto;

    @media (min-width: $md) {
      width: 400px;
      min-height: 650px;
    }
    &--pic-w {
      // transform: translateZ(0.5px) scale(1);
      position: absolute;

      bottom: -30px;
      right: -15%;
      width: 316.54px;
      height: 430px;

      @media (min-width: $xs) {
        width: 526px;
        height: 620px;
        bottom: -30px;
        right: -48%;
      }
      @media (min-width: $md) {
        width: 505px;
        height: 761px;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 3;
        bottom: 0;
        right: 10%;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/woman.webp');
        background-size: contain;
        background-repeat: no-repeat;
        // transform: translateZ(-2px);
      }
    }
    &--pic-2 {
      // transform: translateZ(-0.5px) scale(1);
      position: absolute;
      bottom: 5%;
      left: 10%;
      width: 265.77px;
      height: 265.77px;

      @media (min-width: $xs) {
        width: 409px;
        height: 409px;
        left: -23%;
      }
      @media (min-width: $md) {
        width: 424px;
        height: 424px;
        // left: -70%;
        // bottom: 25%;
        left: -68%;
        bottom: 6%;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/ellipse2.webp');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }

    &--pic-1 {
      // transform: translateZ(-0.5px) scale(1);
      position: absolute;
      top: -10%;
      right: -30%;

      width: 284.57px;
      height: 284.57px;

      @media (min-width: $xs) {
        width: 406px;
        height: 407px;
        top: -60%;
      }
      @media (min-width: $md) {
        width: 454px;
        height: 454px;
        right: -32px;
        top: -5%;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        bottom: 0;
        right: -10px;
        width: 100%;
        height: 100%;
        background-image: url('../images/promo/ellipse.webp');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }
  }
}

.promo--order {
  height: 100%;
  height: calc(100vh - 65px);

  &::after {
    background-image: none;
  }


  /* Portrait and Landscape */
  @media only screen and (min-device-width: 320px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) {
    min-height: 600px;
  }
  /* Portrait and Landscape */
  @media only screen and (min-device-width: 412px) and (max-device-width: 915px) and (-webkit-min-device-pixel-ratio: 2) {
    min-height: 600px;
  }

  & .container {
    min-height: 100vh;
  }

  & .promo__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-bottom: 25px;

    width: 100%;

    @media (min-width: $xs) {
      padding-bottom: 100px;
    }
  }

  & .promo__title {
    max-width: 600px;
  }

  & .promo__text {
    font-size: 1rem;
    max-width: 600px;

    @media (min-width: $xs) {
      font-size: 1.6rem;
    }

    @media (min-width: $md) {
      max-width: 440px;
    }
    @media (min-width: $xl) {
      max-width: 600px;
    }
  }

  & .icon {
    margin-bottom: 10px;
  }

  & .link {
    &:focus,
    &:hover {
      color: $white-color;
    }

    @media (max-width: $xs) {
      font-size: 17px;
    }
  }

  & .promo__pic--pic-1 {
    @media (min-width: 1680px) {
      top: 15%;
    }

    @media (min-width: $sm) and (max-width: $md) {
      width: 303px;
      height: 349px;
      top: -199%;
    }

    /* Portrait and Landscape */
    @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
      width: 303px;
      height: 349px;
      top: -60%;
    }
  }

  & .promo__pic--pic-w {
    @media (min-width: $sm) and (max-width: $md) {
      width: 448px;
      height: 620px;
      bottom: 88%;
      right: -82%;
    }

    /* Portrait and Landscape */
    @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
      width: 560px;
      bottom: 0;
    }

    @media only screen and (min-device-width: 426px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) {
      width: 425px;
    }
  }

  & .promo__pic--pic-2 {
    @media (min-width: $sm) and (max-width: $md) {
      width: 333px;
      height: 350px;
      left: -105%;
      bottom: 35%;
    }

    /* Portrait and Landscape */
    @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    }
  }
}
